<template>
  <Header :dia-semana="this.diaSemana" class='header'></Header>
  <Main :dia-semana='this.diaSemana' ></Main class='main'>
</template>

<script>
import Header from "./components/Header.vue";
import Main from "./components/Main.vue";
export default {
  name: "App",
  components: {
    Header,
    Main,
  },
  data() {
    return {
      diaSemana: null,
    };
  },
  beforeMount() {
    const data = new Date();
    this.diaSemana = data.getDay();
  },
};
</script>

