<template>
  <div class="interfaceWrapper">
    <div
      class="wrapper"
      :class="{ completed: isCompleted }"
      @click="complete()"
    >
      <div v-for="(exec, i) in this.execAux" class="exerciseWrapper">
        <span v-if="i < 1">
          {{ exec }}
          <!-- {{ this.series }} -->
        </span>
        <div v-else class="series">
            <span v-for="(numero, j) in exec" :class="{ reps: j == 0 }" :key='j'>
              {{ numero }} {{ j == 0 ? "x" : "" }}
            </span>
        </div>
      </div>
    </div>

    <button @click="decExec()" class="button">-</button>
  </div>
</template>
<script>
export default {
  name: "Exercicio",
  props: {
    dia: Number,
    exec: Array,
  },
  data() {
    return {
      isCompleted: false,
      series: [],
      execAux: this.$props.exec,
    };
  },
  methods: {
    decExec() {
      if (this.series[0] > 0) {
        this.series[0]--;
      }
      if (this.series[0] == 0) {
        this.isCompleted = true;
      }
    },
  },
  beforeMount() {
    this.$props.exec.forEach((e) => {
      let aux = e.split("");
      aux.forEach((c) => {
        if ((Number(c) || Number(c) === 0) && c != " ") {
          //  console.log(`NÃºmeoro: ${c}`);
          this.series.push(Number(c));
        }
      });
    });
    this.execAux[1] = this.series;
  },
};
</script>
<style>
.interfaceWrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 70vw;
  margin: auto;
}
.wrapper {
  display: flex;
  justify-content: space-evenly;
  width: 50vw;
  margin: auto;
  border: solid 2px black;
}

.wrapper div span {
  width: 2vw;
}
.wrapper div {
  display: flex;
  margin: 1vh;
  padding: 0;
  justify-content: center;
  align-items: center;
  text-align: center;
}
/* .wrapper:hover {
  background-color: rgb(36, 36, 36);
  color: whitesmoke;
} */

.series {
  display: flex;
  width: 5%;
  flex-direction: row;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.exerciseWrapper {
  width: 15vw;
  padding: 0;
  margin: 0;
}
/* .exerciseWrapper div:first-child { 
  margin-right: 10vw;
  color: green;
} */
.reps {
  margin-right: 2vw;
}
.upDown-enter-from,
.upDown-leave-to {
  opacity: 0;
}
.upDown-enter-to,
.upDown-leave-from {
  opacity: 1;
}
.upDown-enter-active,
.upDown-leave-active {
  transition: opacity 0.5s ease;
}
</style>
