<template>
  <div class="mainGrid" @load="test()">
    <!-- <div :v-if="this.tabela[this.dia]" v-for="exec in tabela[this.dia]">
      <section v-for="rep in exec">
        <span>
          {{ rep }}
        </span>
      </section> -->
    <!-- </div> -->

    <div :v-if="this.tabela[this.dia]" v-for="exec in tabela[this.dia]">
      <Exercicio :exec='exec'>

      </Exercicio>
    </div>
  </div>

  <!-- <Exercicio :exec="this.tabela[this.dia]"></Exercicio> -->
</template>
<script>
import Exercicio from "./Exercicio.vue";
export default {
  name: "Tabela",
  components: {
    Exercicio,
  },
  props: {
    diaSemana: Number,
  },
  data() {
    return {
      tabela: {
        domingo: [null],
        segunda: [
          [`Push-ups`, `2x10`],
          [`Dips`, `2x8`],
          [`Incline Push-ups`, `2x12`],
          [`Pike Push-ups`, `2x8`],
          [`Triceps Extension`, `2x10`],
          [`Triceps Dips`, `2x12`],
        ],
        terca: [
          [`Curved Row`, `3x10`],
          [`Barbell Curl ( Regular )`, `3x10`],
          [`Unilateral Biceps Curl`, `3x10`],
          [`Forearm Curl ( Regular )`, `3x8`],
          [`Unilateral Forearm Curl`, `3x8`],
        ],
        quarta: [
          [`Squats`, `3x12`],
          [`Lunges`, `3x8`],
          [`Glute Bridge`, `3x12`],
          [`Calf Raises`, `3x12`],
          [`Sumo Walks`, `2x30s`],
          [`Wall Sit`, `2x30s`],
        ],
        quinta: [
          [`Crunch Kicks`, `3x20`],
          [`Mountain Climber`, `3x20s`],
          [`Russian Twist`, `3x20`],
          [`Plank Knee to Elbow`, `3x20`],
          [`Hollow Body`, `3x15s`],
          [`Couches ( Regular )`, `3x10`],
        ],
        sexta: [
          [`Crunch Kicks`, `3x20`],
          [`Mountain Climber`, `3x20s`],
          [`Russian Twist`, `3x20`],
          [`Plank Knee to Elbow`, `3x20`],
          [`Hollow Body`, `3x15s`],
          [`Couches ( Regular )`, `3x10`],
        ],
        sabado: [null],
      },
      dia: null,
    };
  },
  beforeMount() {
    // console.log(this.$props.diaSemana);
    switch (this.$props.diaSemana) {
      case 0:
        this.dia = `domingo`;
        break;
      case 1:
        this.dia = `segunda`;
        break;
      case 2:
        this.dia = `terca`;
        break;
      case 3:
        this.dia = `quarta`;
        break;
      case 4:
        this.dia = `quinta`;
        break;
      case 5:
        this.dia = `sexta`;
        break;
      case 6:
        this.dia = `sabado`;
        break;
    }
    // console.log(this.tabela[this.dia])
  },
};
</script>
